    mounted () {
      
    },
    components: {
      loading
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import "assets/css/styl/index.styl"

#app {
  max-width: 600px;
  margin: 0 auto;
  overflow: hidden
}

