.vux-loading {
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
  animation-name: vux-loading;
  animation-timing-function: linear;
  border-radius: 99em;
  border: 3px solid #DDD;
  border-left-color: #666;
  display: inline-block;
  width: 16px;
  height: 16px;
