export default {
  state: {
    message: '加载中...',
    isLoading: false,
    userInfo: {}
  },
  mutations: {
    UPDATE_LOADING (state, status) {
      state.isLoading = status
    },
    UPDATE_MESSAGE (state, value) {
      state.message = value
    },
    UPDATE_USERINFO (state, obj) {
      state.userInfo = obj
    }
  },
  actions: {
    UPDATE_LOADING ({ commit }, status) {
      commit('UPDATE_LOADING',status)
    },
    UPDATE_MESSAGE ({ commit }, value) {
      commit('UPDATE_MESSAGE',value)
    },
    UPDATE_USERINFO ({ commit }, obj) {
      commit('UPDATE_USERINFO',obj)
    }
  },
  getters: {
  	getLoading (state) {
      return state.isLoading
    },
    getMessage (state) {
      return state.message
    },
    getUserInfo (state) {
      return state.userInfo
    }
  }
}


